<script>
    import data from '../data/contact.json'
    import MyModal from '../components/MyModal'
    import Multiselect from 'vue-multiselect'

    import axios from "axios";

    export default {
        components: {
            MyModal,
            Multiselect
        },
        data () {
            return {
                data,
                password : "",
                passwordCheck: "",
                email: "",
                partnersData: {
                    "phone_num": "",
                    "address": null,
                    "detail_address": null,
                    "username": null,
                    "password": null,
                    "partner_name": null,
                    "ceo": null,
                    "applicant": null,
                    "email": null,
                    "partner_info": null,
                    "service_area_array" : [],
                },
                modal: false,
                privacy: "",
                idConfirmed: false,
                options: [
                    {
                        value: 'seoul',
                        name: '//'
                    },
                    {
                        value: 'gangwon',
                        name: ''
                    },
                    {
                        value: 'chungcheong',
                        name: ''
                    },
                    {
                        value: 'gyeongsang',
                        name: ''
                    },
                    {
                        value: 'jeolla',
                        name: ''
                    },
                    
                ],
                checked_1: false,
                checked_2: false,
                checked_3: false,
                checked_4: false,
                checked_5: false,
                profile_image: null,
                registration_image: null,
                partnerPk: null
            }
        },
        methods: {
            handlePFileUpload() {
                this.profile_image = this.$refs.pro_image.files[0]
            },
            handleRFileUpload() {
                this.registration_image = this.$refs.reg_image.files[0]
            },
            async sendRegisterData() {
                if(this.checked_1) {
                    this.partnersData.service_area_array.push('seoul')
                }
                if(this.checked_2) {
                    this.partnersData.service_area_array.push('gangwon')
                }
                if(this.checked_3) {
                    this.partnersData.service_area_array.push('chungcheong')
                }
                if(this.checked_4) {
                    this.partnersData.service_area_array.push('gyeongsang')
                }
                if(this.checked_5) {
                    this.partnersData.service_area_array.push('jeolla')
                }
                console.log(this.partnersData)
                await axios.post('http://tmdgud1112.pythonanywhere.com/api/partner/', this.partnersData).then(res=>{
                    this.partnerPk = res.data.id
                });
                const bodyFormData = new FormData();

                bodyFormData.append('profile_image', this.profile_image)
                bodyFormData.append('registration_image', this.registration_image)
                bodyFormData.append('partner', this.partnerPk)


                await axios.post('http://tmdgud1112.pythonanywhere.com/api/partnerimage/', bodyFormData,{ headers: { 'Content-Type': 'multipart/form-data' }}).then(res=>{
                    alert("  .        .")
                    this.$router.push('/')
                });
                
            },
            validEmail: function(email) {
                var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(email);
            },
            numeric() {
                if(/\D/.test(this.partnersData.phone_num)) {
                    return true
                }
                else {
                    return false
                }
            },
            length() {
                if( this.partnersData.phone_num && this.partnersData.phone_num.length != 11) {
                    return true
                }
                else {
                    return false
                }
            },
            openModal() {
                this.modal = true
            },
            confirmModal(data) {
                this.partnersData.address = data.roadAddress
            },
            closeModal() {
                this.modal = false
            },
            idReset() {
                this.partnersData.username = ""
            },
            idConfirm() {
                this.idConfirmed = true
            },
            async idCheck() {
                var username = { "username": ""}
                username.username = this.partnersData.username
                await axios.post(
                    'http://tmdgud1112.pythonanywhere.com/api/checkid/', username
                ).then(res=>{
                    alert(" .")
                    this.idConfirm()
                }).catch(err=>{
                    alert("  .")
                    this.idReset()
                })
            }
        }
    };
</script>

