<template>
    <div class="main-container">

        <Header />

        <Breadcrumb :items="items" title=" " />
        <div v-if="!isLogin">
            <ClientLoginInputForm 
                :estData="estData"
                @setEstData="setEstData"
                @setClientStatus="setClientStatus" 
                @login="isLogin=true"
            />
        </div>

        <div v-if="isLogin && clientStatus == 'X'">
            <ClientXCheck
            :estData="estData"
            />
        </div>

        <div v-if="isLogin && clientStatus == 'S' ">
            <ClientCheckWrapper 
                :estData="estData" 
                @setstatus="setClientStatus"
                />
        </div>

        <div v-if="isLogin && clientStatus == 'D'">
            <ClientDCheck
            :estData="estData" 
            />
        </div>
        <Footer />

        <OffCanvasMobileMenu />

        <!-- back to top start -->
        <back-to-top class="scroll-top" bottom="60px">
            <i class="ion-android-arrow-up"></i>
        </back-to-top>
        <!-- back to top end -->
    </div>
</template>

<script>
    import Header from '@/components/Header';
    import Breadcrumb from '../components/Breadcrumb'
    import ClientCheckWrapper from '../components/sections/ClientCheckWrapper'
    import ClientLoginInputForm from '../components/ClientLoginInputForm'
    import ClientXCheck from '../components/ClientXCheck'
    import ClientDCheck from '../components/ClientDCheck'
    import BrandCarousel from '../components/BrandCarousel'
    import Footer from '../components/Footer'
    import OffCanvasMobileMenu from '@/components/OffCanvasMobileMenu';

    import EventBus from '../main.js'
    import axios from 'axios'
    export default {
        props: ['id'],
        components: {
            Header,
            Breadcrumb,
            BrandCarousel,
            Footer,
            OffCanvasMobileMenu,
            ClientCheckWrapper,
            ClientLoginInputForm,
            ClientXCheck,
            ClientDCheck
        },
        data() {
            return {
                items: [
                    {
                        text: '      .'
                    },
                    {
                        text: ''
                    }
                ],
                estData: {},
                isLogin: false,
                clientStatus: false
            } 
        },
        methods: {
            setEstData (data) {
                this.estData = data
            },
            loginMounted () {
                this.isLogin = true
            },
            setClientStatus(code) {
                this.clientStatus = code
                console.log(this.clientStatus)
            }
        },
        metaInfo: {
            title: 'Castro - Contact Us',
            htmlAttrs: {
                lang: 'en',
                amp: true
            }
        },
        async mounted () {
            if(this.id == 'homepage') {
                console.log("  ")
            }
            else {
                await axios.get('http://tmdgud1112.pythonanywhere.com/api/clientcheck/', {params: {verify_code: this.id}}).then(res=>{
                    if(res.data.msg == "STATUS_X") {
                        this.estData = res.data.results
                        this.clientStatus = 'X'
                        this.loginMounted()
                    }
                    else if(res.data.msg == "STATUS_S") {
                        this.estData = res.data.results
                        this.clientStatus = 'S'
                        this.loginMounted()
                    }
                    else if(res.data.msg == "STATUS_D") {
                        this.estData = res.data.results
                        this.clientStatus = 'D'
                        this.loginMounted()
                    }
                    else if(res.data.msg == "STATUS_C") {
                        this.estData = res.data.results
                        this.clientStatus = 'C'
                        this.loginMounted()
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
            }
        }
    }
</script>

